.Footer {

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 105px;

  position: relative;
  margin-bottom: 195px;



  &::after{
    content: '';
  }

  @media screen and (max-width: 600px){
    gap: 71px;
  }

  .FooterTitle {
    display: flex;
    gap: 33px;
    color: #B4F299;
    font-size: 34px;

    @media screen and (max-width: 1200px){
      text-align: center;
      flex-direction: column;
      align-items: center;
    }

    @media screen and (max-width: 600px){
      gap: 15px;
      font-size: 18px;
      .FooterEmail{
        font-size: 18px;
      }
    }
  }

  .FooterEmail {
    color: #B4F299;
    font-weight: 800;
    font-size: 34px;
    text-decoration: underline;
  }

}