@import "../../styles/variables";

.ContactLink {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  opacity: .5;
  border-radius: 20px;

  width: 140px;
  padding: 1px;

  &:hover{
    opacity: 1;
    background: linear-gradient(139.69deg, #05030E 3.61%, #7EE8A0 114.72%);
  }

  .ContactWrapper {

    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 20px;

    width: 100%;
    padding: 30px;

    background-color: $background;

    gap: 17px;

    img {
      box-shadow: 0px 24px 64px rgba(138, 249, 173, 0.3);
    }


    .ContactTitle {
      background: $horizontal-gradient;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;

      font-weight: bold;
      font-size: 14px;
    }
  }
}