@import "../../styles/variables";

.Level {

  display: flex;
  flex-direction: column;
  gap: 5px;


  .Label {
    display: flex;
    align-items: center;
    gap: 25px;

    @media screen and (max-width: 420px) {
      flex-direction: column;
      align-items: flex-start;
      gap: 5px;

      .Separator{
        display: none;
      }
    }

    .Title {
      font-weight: 500;
      font-size: 14px;
      color: white;
    }

    .Separator {
      margin-top: 2px;
      width: 36px;
      height: 1px;
    }

    .Text {
      font-weight: 500;
      font-size: 16px;
    }
  }

  .Information {
    display: flex;
    align-items: center;
    gap: 29px;


    .ProgressBar {
      width: 100%;
      height: 8px;
      background: #c4c4c442;
      border-radius: 25px;
      box-shadow: 0px 14px 44px rgba(220, 152, 57, 0.6);

      .ProgressBarInner {
        height: 100%;
        border-radius: 25px;
      }
    }

    .Percent {
      font-weight: 800;
      font-size: 26px;
    }
  }

  &.GreenLevel {
    .Separator {
      background: $horizontal-gradient
    }

    .Text {
      background: $horizontal-gradient;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }

    .ProgressBarInner {
      background: $horizontal-gradient;
    }

    .Percent {
      background: $horizontal-gradient;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }

  &.LightOrangeLevel {
    .Separator {
      background: linear-gradient(139.33deg, #CAEE39 -14.59%, #E99A3D 99.86%);
    }

    .Text {
      background: linear-gradient(139.33deg, #CAEE39 -14.59%, #E99A3D 99.86%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }

    .ProgressBarInner {
      background: linear-gradient(139.33deg, #EEDC39 -14.59%, #E99A3D 99.86%);

    }

    .Percent {
      background: linear-gradient(139.33deg, #CAEE39 -14.59%, #E99A3D 99.86%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }

  &.OrangeLevel {
    .Separator {
      background: linear-gradient(139.33deg, #CAEE39 -14.59%, #E99A3D 99.86%);
    }

    .Text {
      background: linear-gradient(139.33deg, #EED139 -14.59%, #E9713D 99.86%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }

    .ProgressBarInner {
      background: linear-gradient(139.33deg, #EED139 -14.59%, #E9713D 99.86%);
    }

    .Percent {
      background: linear-gradient(139.33deg, #EED139 -14.59%, #E9713D 99.86%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }

  &.RedLevel {
    .Separator {
      background: linear-gradient(139.33deg, #EE6439 -14.59%, #E9713D 99.86%);    }

    .Text {
      background: linear-gradient(139.33deg, #EE6439 -14.59%, #E9713D 99.86%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }

    .ProgressBarInner {
      background: linear-gradient(139.33deg, #EE6439 -14.59%, #E9713D 99.86%);    }

    .Percent {
      background: linear-gradient(139.33deg, #EE6439 -14.59%, #E9713D 99.86%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }
}